
/* ######### ground ###### */
.ground{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.ground .groundBox{
    /* border:2px solid red; */
    width: 90%;
    height: 90vh;
    position: relative;

}

/* ######### box ###### */
.ground .groundBox .box{
    position: absolute;
    top: 0px;
    left: 0px;
    border-radius: 2px;
    transition: all 0.5s ease-in;
}
/* boxImg */
.boxImg{
    width:60px;
    position: relative;
    user-select: none;
}
.activeFlower{
    cursor: grabbing;
}



/* throwBtn */
.infoBox{
    position: absolute;
    top:10px;
    left:10px;
    display: flex;
    justify-content: space-between;
    width:100px;
}
.btn{
    border:none;
    outline:none;
    border-radius: 20px;
    width:40px;
    height:40px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.3);
}
.btn.info{
    position: relative;
}
.btn:active{
    opacity: 0.5;
}
.infoContent{
    top: 50px;
    position: absolute;
    width:200px;
    padding: 10px;
    border:1px solid #999;
    border-radius: 10px;
    display: none;

}
.btn.info:hover .infoContent{
    display: block;
}

.btn img{
    width:20px;
}
.throwBtn img{
    width:70%;
    filter: invert(100%);
    user-select: none;
    
}
.throwBtn:hover{
    background: rgba(30, 144, 255,0.5);
}





@keyframes moveCircle {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}



